

















export default {
	props: {
		visible: {
			type: Boolean,
			default: false,
			required: true
		},
		title: {
			type: String,
			default: '提示'
		},
		mainColor: {
			type: String,
			default: '#e74a39'
		},
		defaultValue: {
		  type: String,
		}
	},
	mounted() {
		this.value = this.defaultValue;
	},
	data() {
		return {
			value: '',
		};
	},
	methods: {
		close() {
			this.$emit('update:visible', false);
		}
	}
};
